:root {
  --main-color: #00acdc;
}

body, ul {
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  margin-left: 50px;
  margin-right: 50px;
  display: block;
}

.logo .img {
  vertical-align: middle;
}

.nav {
  margin: 0 50px 0 0;
  display: flex;
}

.list {
  align-items: center;
  list-style: none;
  display: flex;
}

.list_item {
  margin-right: 20px;
}

.list_item:last-child {
  margin-right: 0;
}

.nav_link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.nav_link:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
